import { Component, OnInit, Output, EventEmitter, ViewChild, TemplateRef, HostBinding, ViewEncapsulation, ElementRef, Input, SkipSelf, OnChanges, SimpleChanges, ContentChild, AfterContentInit, AfterViewInit } from '@angular/core';
import { SolutionService } from '../service/solution.service';
import { LocaleService } from '@farris/ui-locale';
import { DialogComponent } from '@farris/ui-dialog';
import { QueryCondition } from '../entity/querycondition';
import { MultiSelectComponent } from '@farris/ui-multi-select';
import { NotifyService } from '@farris/ui-notify';
@Component({
    selector: 'app-queryconfigdialog',
    templateUrl: './configdialog.component.html',
    styleUrls: ['./configdialog.component.css'],
    encapsulation: ViewEncapsulation.None
})
export class ConfigDialogComponent implements OnInit, OnChanges, AfterViewInit {
    @Input() inputSelectedFileds: Array<QueryCondition> = [];
    @Input() filedsDataSource: Array<QueryCondition>;
    @Output() cancelEmitter: EventEmitter<any> = new EventEmitter<any>();
    @Output() confirmEmitter: EventEmitter<any> = new EventEmitter<any>();
    @ViewChild('dialog') dialog: DialogComponent;
    @ViewChild('searchMulti') multiSelectCmp: MultiSelectComponent;

    selectedIds: Array<string> = [];
    title: string;
    visibleFields: Array<QueryCondition>;
    visibleSelectedFields: Array<QueryCondition>;
    constructor(private solutionService: SolutionService, private localeService: LocaleService, private notify: NotifyService) { }

    ngOnChanges(changes: SimpleChanges): void {
        if(changes.filedsDataSource && changes.filedsDataSource.currentValue) {
            this.visibleFields = this.filedsDataSource.filter(item => {return !!item.visible});
        }

        if (changes.inputSelectedFileds && changes.inputSelectedFileds.currentValue) {
            this.visibleSelectedFields = this.inputSelectedFileds.filter(item => {return !!item.visible});
            if(this.visibleSelectedFields.length) {
                this.selectedIds = this.visibleSelectedFields.map(qc => qc.id);
            }
        }
    }

    ngOnInit() {
        this.title = this.localeService.getValue('queryCondition.container.config');
        if(this.filedsDataSource) {
            this.visibleFields = this.filedsDataSource.filter(item => {return !!item.visible});
        }
    }

    ngAfterViewInit(): void {
        if(this.multiSelectCmp.enableMoveMore !== undefined) {
            this.multiSelectCmp.enableMoveMore = true
        }
    }
    /**
     * 
     */
    cancel() {
        this.selectedIds = this.visibleSelectedFields.map(qc => qc.id);
        //undo: selectedIdmoveAllToLeft
        if(!this.selectedIds.length) {
            this.multiSelectCmp.moveAllToLeft()
        }
        this.cancelEmitter.emit(true);        
        this.multiSelectCmp.searchValue = '';
        this.dialog.close();
    }

    /**
     * 
     */
    confirm() {

        //
        let requiredAndUncheck  = this.visibleFields.filter(val => val.control && val.control.isRequired()).filter(item => !this.selectedIds.includes(item.id))
        if(requiredAndUncheck && requiredAndUncheck.length) {
            let msg = requiredAndUncheck.map(val => val.fieldName).join('') + ''
            this.notify.error(msg)
            return 
        }
        
        const selectedFields = [];
        let _fieldData: QueryCondition;
        this.selectedIds.forEach(id => {
            const index = this.visibleSelectedFields.findIndex(field => field.id == id);
            if (index > -1) {
                selectedFields.push(this.visibleSelectedFields[index].fieldCode);
            } else {
                _fieldData = this.visibleFields.find(field => field.id == id);
                _fieldData.value.clearValue();
                selectedFields.push(_fieldData.fieldCode);
            }
        });
        this.confirmEmitter.emit(selectedFields);
        this.multiSelectCmp.searchValue = '';
        this.dialog.close();
    }

    /**
     * 
     */
    reset() {

    }
}
