import { IControl } from "../icontrol";
import { ControlType } from "../../controltype";

/**
 * 
 */
export class SmartHelpContol implements IControl {

    /**
     * 
     */
    getControlType(): ControlType {
        return ControlType.SmartHelp;
    }
    isRequired(): boolean {
        return !!this.require;
    }

    /**
     * 
     */
    uri: string;

    /**
     * 
     */
    textField: string;

    /**
     * 
     */
    valueField: string;

    /**
     * 
     */
    mapFields: any;

    /**
     * 
    */
    idField: any;

    /**
     * 
    */
    displayType: any;

    /**
     * 
    */
    context: any;

    /**
     * 
    */
    preEventCmd: any;

    /**
    * 
    */
    postEventCmd: any;

    /**
     * 
    */
    className: string;

    /**
     * 
     */
    enableFullTree: boolean;

    /**
     * 
     */
    loadTreeDataType: string;

    /**
     * 
     */
    singleSelect: boolean;

    /** */
    expandLevel: number;

    /**
     * 
     */
    enableCascade: boolean;

    /**
     *  enable: , up, down, disable
     */
    cascadeStatus: "enable" | "up" | "down" | "disable";

    /**
     * 
     */
    placeholder: string;

    /**
     * 
     */
    require: boolean;

    //nosearchtrue
    nosearch: boolean;

    /**
     * 
     */
    displayFields: string;

    /**
     * 
     */
    displayTextSeparator: string;

    /**
     * 
     */
    editable: boolean;

    /**
     * 
     */
    clearFields: string;

    /**
     * 
     */
    clear: any;
    /**
     * 
     */
    pageSize: number;
    /**
     * 
     */
    pageList: string;
   // 
   dialogTitle: string;
   // 
   panelWidth: number;
   // 
   panelHeight: number;
   // 
   quickSelect: QuickSelectOption;
   // 
   enableMultiFieldSearch: boolean;
}

export class QuickSelectOption {
    enable: boolean;
    showItemsCount?: number;
    formatter?: (data: any) => string;
    showMore?: boolean;
}