import { IControl } from "../icontrol";
import { ControlType } from "../../controltype";

/**
 * 
 */
export class SingleMonthControl implements IControl {

    /**
     * 
     */
    getControlType(): ControlType {
        return ControlType.SingleMonth;
    }

    isRequired(): boolean {
        return !!this.require;
    }

    /**
     * 
     */
    format: string;
    
    /**
     * 
     */
    returnFormat:string;
    
    /**
     * 
     */
    dateChangedCmd: any;
    
    /**
     * 
     */
    className: string;
    placeholder: string;
    require: boolean;
}