import { Component, Input, OnInit, Injector, Output } from '@angular/core';
import { ControlType } from '../../entity/controltype';
import { IdService } from '@farris/ui-common';
import { StateService } from '../service/state.service'
@Component({
  selector: 'querycondition-item',
  templateUrl: './querycondition-item.component.html',
  styleUrls: ['./querycondition-item.component.css']
})
export class QueryconditionItemComponent implements OnInit {


  controlType = ControlType;
  @Input() item
  fieldCodeOfClearFields: Array<string> = [];
  private idService: IdService;
  private stateSer: StateService;
  constructor(private injector: Injector) {
    this.idService = this.injector.get(IdService);
    this.stateSer = this.injector.get(StateService);
  }

  ngOnInit() {
  }

  conditionChange() {
    this.stateSer.updateQueryConditionListData()
  }

  getStyle(a) {
    return ''
  }
  buildSmartHelpKey(value: string) {
    let prefix = '';
    if (location.hash) {
      const pathArr = location.hash.split('?');
      prefix = pathArr ? pathArr[0] : '';
    } else {
      const pathArr = location.pathname.split('/');
      prefix = pathArr ? pathArr[pathArr.length - 1] : '';
    }
    return this.idService.encrypt(prefix + '/querysolution/' + value);
  }
  clearFieldsChange(event) {
  }
}
