import { Component, Input, OnInit, Optional, Output, EventEmitter, OnDestroy, ElementRef, Injector, ViewChild, AfterViewInit } from '@angular/core';

import { QueryconditionAdvancedData } from './type/type'
import { StateService } from './service/state.service';
import { QueryCondition } from '../entity/queryCondition'
import { NotifyService } from '@farris/ui-notify';
import { cloneDeep } from 'lodash-es';
import { SolutionService } from '../service/solution.service';
import { ControlType } from '../entity/controltype';
import { QuerySolutionVo } from '../entity/querysolutionvo'
import { MessagerService } from '@farris/ui-messager';
import { LocaleService } from '@farris/ui-locale';
import { Subscription } from 'rxjs/internal/Subscription';
@Component({
  selector: 'querycondition-advanced',
  templateUrl: './querycondition-advanced.component.html',
  styleUrls: ['./querycondition-advanced.component.css'],
  providers: [SolutionService]
})

export class QueryconditionAdvancedComponent implements OnInit, OnDestroy, AfterViewInit {

  queryconditionData: QueryconditionAdvancedData
  _selectSolution: QuerySolutionVo
  @Input() get selectSolution() {
    return this._selectSolution
  }
  set selectSolution(value: QuerySolutionVo) {
    if (value && this._selectSolution && value.id === this._selectSolution.id) {
      return
    }
    this._selectSolution = value
    this.queryconditionData = value.queryConditionsAdvanced
    this.stateSer.setQueryconditionData(value.queryConditionsAdvanced)
  }
  @Input() get fieldConfig() {
    return this._fieldConfig
  }
  set fieldConfig(value) {
    //undo  
    //doing rangeList

    let _value = cloneDeep(value)
    _value.forEach(val => {
      if (val.control.controltype === 'single-date') {
        val.control.isDynamicDate = false
      } else if(val.control.controltype === 'radio') {
        val.control.horizontal = true
      }
      
    })
    //false
    let modifyFieldConfig = this.rangeToSimpleControlType(_value)
    this._fieldConfig = this.solutionService.convertToQueryConditions(modifyFieldConfig);
    //false
    this._fieldConfig.forEach(val => {
      if (val.control.getControlType() === 8 && !val.value.getOriginalValue().length) {
        val.value.setOriginalValue([false])
      }
    })
    this.stateSer.changeFieldConfig(this._fieldConfig)

  }
  _fieldConfig: Array<QueryCondition>
  dataSubscribe: Subscription = null
  dataListSubscribe: Subscription = null
  _self = null
  //truefalse

  isLimitHeight = true
  @Output('conditionChange') conditionChange = new EventEmitter();

  @ViewChild('advancedContent') advancedContent: ElementRef;
  @ViewChild('advancedElement') advancedElement: ElementRef;
  constructor(private notifySer: NotifyService, private msgSer: MessagerService, private localeService: LocaleService, private solutionService: SolutionService, private stateSer: StateService, private injector: Injector) {

    // this.fieldConfig = this.stateSer.getFieldConfig()
    this.dataSubscribe = this.stateSer.queryconditionData$.subscribe((data: QueryconditionAdvancedData) => {
      this.queryconditionData = data
      this.selectSolution.queryConditionsAdvanced = data
    })
    this.dataListSubscribe = this.stateSer.queryconditionListData$.subscribe((queryconditionListData: Array<QueryCondition>) => {
      this.conditionChange.emit(queryconditionListData);

    })
    this._self = this.injector.get(ElementRef);

  }

  ngAfterViewInit(): void {
    //resize
    setTimeout(()=>{this.handleResizeChange()},1000)
  }

  ngOnInit() {}

  ngOnDestroy(): void {
    this.dataSubscribe.unsubscribe()
    this.dataListSubscribe.unsubscribe()
  }

  //
  addItem() {
    if (!this.fieldConfig.length) {
      this.notifySer.error('')
    } else {

      let item = cloneDeep(this.fieldConfig[0])
      this.stateSer.changeQueryconditionData({ item, path: [1] }, 'add', true)
      this.advancedContent.nativeElement.scrollTo({ top: this.advancedContent.nativeElement.scrollHeight })
    }
  }

  //
  addGroup() {
    this.stateSer.addGroup()
  }

  //
  reset() {
    this.msgSer.confirm(this.localeService.getValue('advancedMode.info.resetInfo')).subscribe((isConfirm) => {
      if (isConfirm) {
        this.stateSer.reset()
        //
        let event = document.createEvent('Event')
        event.initEvent('keydown', true, false)

        event = Object.assign(event, {
          ctrlKey: false,
          metaKey: false,
          altKey: false,
          which: 13,
          keyCode: 13,
          key: 'Enter',
          code: 'Enter'
        })
        this._self.nativeElement.dispatchEvent(event)
      }
    });

  }


  rangeToSimpleControlType(configs) {
    let unformatConfigs = cloneDeep(configs)
    unformatConfigs.forEach(val => {
      if (val.control && val.control.controltype) {

        val.control.controltype = this.rangeToSimpleTypeModify(val.control.controltype)
      }

    })
    return unformatConfigs

  }

  //
  rangeToSimpleTypeModify(controlType) {

    let result = ''
    switch (controlType) {

      case 'date':
        result = 'single-date'
        break
      case 'date-time':
        result = 'single-date-time'
        break
      case 'month':
        result = 'single-month'
        break
      case 'number':
        result = 'single-number'
        break
      default:
        result = controlType
    }
    return result
  }

  /**
   * 
   */
  handleResizeChange() {
    //@ts-ignore
    const myObserver = new ResizeObserver(entries => {
      entries.forEach(entry => {
        if(entry.target.style.height < 431 ) {
          this.isLimitHeight = true
        } else {
          this.isLimitHeight = false
        }
      })
    })
    myObserver.observe(this.advancedElement.nativeElement)
  }

}
